.activitywrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 2em 0 2em;
}
.day {
  margin-bottom: 2em;
}
.row {
  min-width: 30rem;
  max-width: 35rem;
}

.table th:nth-child(1) {
  width: 50px;
  margin-left: 1em;
}

.table th:nth-child(2) {
  padding-left: 2em;
}
.tableContainer {
  height: 30rem;
  overflow-y: auto;
}
// .tableBody {
//   display: block;
//   max-height: 25rem;
//   overflow-y: auto;
// }
@media only screen and (max-width: 946px) {
  .activitywrap {
    height: 23rem;
  }
  .row {
    min-width: 0;
    max-width: 35rem;
  }
  // .tableBody {
  //   display: block;
  //   max-height: 25rem;
  //   overflow-y: auto;
  // }
  .tableContainer {
    height: 25rem;
    overflow-y: auto;
  }
}
