.inputwrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.button {
  margin-top: 2.2em;
  margin-left: 3em;
  max-width: 12em;
}
.input {
  width: 12em;
}
.radiobutton {
  width: 5em;
}
.commentAndSend {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.inputFieldNewAnswerToPoll {
  outline-color: oklch(var(--p)) !important;
  outline-offset: none !important;
  outline-width: 0.3px !important;
  outline-style: solid !important;
}

.inputFieldNewAnswerToPoll:focus,
inputFieldNewAnswerToPoll:focus-within {
  outline-color: oklch(var(--p)) !important ;
  outline-width: none !important;
  outline-offset: none !important;
  outline-style: solid !important;
}

@media (max-width: 846px) {
  .inputwrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .input {
    margin-bottom: 0.7em;
  }
  .radiobutton {
    margin-right: 1em;
  }
  .button {
    width: 8rem;
    margin-left: 1em !important;
  }
  .radioButtons {
    max-width: 4em;
    margin-bottom: 1em;
  }
  .commentAndSend {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.5em;
    margin-left: 1.2em;
  }
  .button {
    margin-right: 0.7em;
    margin-top: 0;
    margin-left: 0;
  }
  .nameWrapp {
    margin-bottom: 2em;
    max-width: 9em;
    margin-left: 0.5em;
  }
  .input {
    max-width: 8em;
  }
}
