.App {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 946px) {
  .App {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
}
