.headline {
  display: flex;
  justify-content: flex-start;
  margin: 1em 0 2em 0;
  justify-content: center;
  margin-right: 2em;
}
h1 {
  text-align: center;
}

@media only screen and (max-width: 946px) {
  .headline {
    display: flex;
    justify-content: flex-start;
    justify-content: center;
    margin-right: 2em;
  }
}
