.createNewPollcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.datePicker {
  width: 15em;
}
.formSelectDay {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  height: 3rem;
}
.weekDay {
  width: 20rem;
}
.toggleDayOrCalender {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dayBetweenText {
  padding: 3em;
  width: 20em;
  margin-top: 2em;
}

.reAccuringWrapp {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.inputFieldNewPoll {
  outline-color: oklch(var(--p)) !important;
  outline-offset: none !important;
  outline-width: 0.3px !important;
  outline-style: solid !important;
  max-width: 9em !important;
}

.inputFieldNewPoll:focus,
inputFieldNewPoll:focus-within {
  outline-color: oklch(var(--p)) !important ;
  outline-width: none !important;
  outline-offset: none !important;
  outline-style: solid !important;
}
.headerOfNewPollContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.saveNewPoll {
  margin-top: 2em;
}
.toggleReaccuring {
  margin-top: 0.8em;
}
.saveWrapInCreateNewPoll {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin: 1em;
  margin-right: 1.9em;
}
.NewUrlInput {
  margin-top: 1.7em;
  width: 25em;
  display: none;
}
.timeInputContainer {
  margin-top: 0.2em;
}
.toggleDayOrCalenderComponent {
  margin-left: 2em;
}
.setTimeHereContainer {
  margin-left: 3.1em;
  margin-bottom: 1.3em;
}
.reaccuringBlock {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}
.dropdownWrap {
  display: flex;
  padding-bottom: 2rem;
}
.Active {
  @extend .NewUrlInput;
  display: block;
  margin-left: 0.7em;
}
.select {
  width: 16rem;
  margin-left: 2rem;
}
.nameOnPollContainer {
  display: flex;
  justify-content: center;
}

@media (max-width: 844px) {
  .timeInputContainer {
    display: flex;
    flex-direction: column;
    margin-left: 0;
    margin-top: 1em;
  }
  .nameOnPollContainer {
    display: flex;
    margin-left: 1em;
  }
  .saveNewPoll {
    margin-top: 0;
  }
  .saveWrapInCreateNewPoll {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 1.5em;
    margin-right: 1em;
  }
  .formSelectDay {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20rem;
    height: 3rem;
  }
  .reAccuringWrapp {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .inputLinkGenerated {
    max-width: 20rem !important;
  }
  .dayBetweenText {
    width: 20em;
    padding: 0;
    margin-bottom: 1em;
    margin-top: 2em;
  }
  .setTimeHereContainer {
    margin-left: 0;
    margin-top: 0.5em;
  }
}
