.wrapper {
  display: flex;
  margin-top: 2rem;
  justify-content: center;
}
.container {
  display: flex;
  flex-direction: column;
  height: 50rem;
  padding-bottom: 2em;
}
.test {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.spacer {
  flex: 1 0 auto;
}

.input {
  border-width: 0px;
  width: 50%;
}
.day {
  margin: 2em;
  min-width: 25rem;
}

@media only screen and (max-width: 946px) {
  .day {
    min-width: 20rem;
    margin: 0;
  }
}
